import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import storeupList from '@/views/pages/storeup/list'
import fuwuxiangmuList from '@/views/pages/fuwuxiangmu/list'
import fuwuxiangmuDetail from '@/views/pages/fuwuxiangmu/formModel'
import fuwuxiangmuAdd from '@/views/pages/fuwuxiangmu/formAdd'
import baoyangyuyueList from '@/views/pages/baoyangyuyue/list'
import baoyangyuyueDetail from '@/views/pages/baoyangyuyue/formModel'
import baoyangyuyueAdd from '@/views/pages/baoyangyuyue/formAdd'
import fuwuleixingList from '@/views/pages/fuwuleixing/list'
import fuwuleixingDetail from '@/views/pages/fuwuleixing/formModel'
import fuwuleixingAdd from '@/views/pages/fuwuleixing/formAdd'
import shangjiaList from '@/views/pages/shangjia/list'
import shangjiaDetail from '@/views/pages/shangjia/formModel'
import shangjiaAdd from '@/views/pages/shangjia/formAdd'
import cartList from '@/views/pages/shop_order/cart'
import tixingyutongzhiList from '@/views/pages/tixingyutongzhi/list'
import tixingyutongzhiDetail from '@/views/pages/tixingyutongzhi/formModel'
import tixingyutongzhiAdd from '@/views/pages/tixingyutongzhi/formAdd'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'fuwuxiangmuList',
			component: fuwuxiangmuList
		}, {
			path: 'fuwuxiangmuDetail',
			component: fuwuxiangmuDetail
		}, {
			path: 'fuwuxiangmuAdd',
			component: fuwuxiangmuAdd
		}
		, {
			path: 'baoyangyuyueList',
			component: baoyangyuyueList
		}, {
			path: 'baoyangyuyueDetail',
			component: baoyangyuyueDetail
		}, {
			path: 'baoyangyuyueAdd',
			component: baoyangyuyueAdd
		}
		, {
			path: 'fuwuleixingList',
			component: fuwuleixingList
		}, {
			path: 'fuwuleixingDetail',
			component: fuwuleixingDetail
		}, {
			path: 'fuwuleixingAdd',
			component: fuwuleixingAdd
		}
		, {
			path: 'shangjiaList',
			component: shangjiaList
		}, {
			path: 'shangjiaDetail',
			component: shangjiaDetail
		}, {
			path: 'shangjiaAdd',
			component: shangjiaAdd
		}
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'tixingyutongzhiList',
			component: tixingyutongzhiList
		}, {
			path: 'tixingyutongzhiDetail',
			component: tixingyutongzhiDetail
		}, {
			path: 'tixingyutongzhiAdd',
			component: tixingyutongzhiAdd
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
